/* hamburger ====================== */
.hamburger {
  position: relative;
  display: none;
  margin-left: auto;
  margin-right: 15px;
  width: 60px;
  height: 130px;
  top: 0;
  z-index: 10;
  transition: height 0.3s linear; }

.header__scrolled .hamburger {
  height: 80px; }

.hamburger1,
.hamburger2,
.hamburger3 {
  position: absolute;
  top: 50%;
  left: 0;
  transition: all 200ms ease-in-out;
  z-index: 10;
  width: 60px;
  height: 8px;
  border-radius: 5px;
  background-color: #a12b2a; }

.hamburger:hover .hamburger1,
.hamburger:hover .hamburger2,
.hamburger:hover .hamburger3 {
  background: #a12b2a; }

.convert:hover .hamburger2 {
  background: inherit; }

.hamburger1 {
  top: calc(50% - 16px); }

.hamburger3 {
  top: calc(50% + 16px); }

/* animation hamburger line 1 and 3 convert to 45deg, middle line - color inerit */
.convert .hamburger1 {
  top: 50%;
  transform: rotate(45deg); }

.convert .hamburger2 {
  background-color: transparent; }

.convert .hamburger3 {
  top: 50%;
  transform: rotate(-45deg); }

/* hamburger end ====================== */
/* submenu start ====================== */
.sub-menu-parent {
  position: relative; }

.sub-menu {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  transform: translateY(-2em);
  z-index: -1;
  transition: all 0.3s ease-in-out 0s, visibility 0s linear 0.3s, z-index 0s linear 0.01s; }

.sub-menu-parent:focus .sub-menu,
.sub-menu-parent:focus-within .sub-menu,
.sub-menu-parent:hover .sub-menu {
  visibility: visible;
  opacity: 1;
  z-index: 1;
  transform: translateY(0%);
  transition-delay: 0s, 0s, 0.3s; }

